import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';

class CustomerOnlyRegistration extends Component {

    constructor() {
        super();
        this.state = {
            // name: '',

            firstName: '',
            lastName: '',
            userName: '',
            passWord: '',
            status: '',

            creditCardNum: '',
            username: ''



            // address: '',
            // city: '',
            // phone: '',
            // email: ''
        };
    }
    onChange = (e) => {
    const state = this.state
    state[e.target.name] = e.target.value;
    this.setState(state);
}

onSubmit = (e) => {
    e.preventDefault();

    const { firstName, lastName, userName, passWord, status, creditCardNum, username } = this.state;

    // axios.post('/creditCards', { creditCardNum, username })
    //     .then((result) => {
    //     this.props.history.push("/")
    // });

    function postUser(){
        return axios.post('/users', { firstName, lastName, userName, passWord, status })
    }

    function postCreditCard(){
        return axios.post('/creditCards', { creditCardNum, username })
    }

    axios.all([postUser(), postCreditCard()])
        .then(axios.spread((res1, res2)=>{
            this.props.history.push("/")
            this.props.history.push("/")
        }));


}

render() {
    const { firstName, lastName, userName, passWord, status, creditCardNum, customerUsername } = this.state;
    return (
        <div class="container">
        <div class="panel panel-default">
        <div class="panel-heading">
        <h3 class="panel-title">
        Customer Registration
    </h3>
    </div>
    <div class="panel-body">
        <h4><Link to="/"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Users List</Link></h4>
    <form onSubmit={this.onSubmit}>


        <div class="form-group">
        <label for="isbn">Credit Card #:</label>
    <input type="text" class="form-control" name="creditCardNum" value={creditCardNum} onChange={this.onChange} placeholder="Credit Card" />
        </div>

        <div class="form-group">
        <label for="isbn">First Name:</label>
    <input type="text" class="form-control" name="firstName" value={firstName} onChange={this.onChange} placeholder="First Name" />
        </div>

        <div class="form-group">
        <label for="isbn">User Name:</label>
    <input type="text" class="form-control" name="userName" value={userName} onChange={this.onChange} placeholder="User Name" />
    // <input type="text" class="form-control" name="customerUsername" value={customerUsername} onChange={this.onChange} placeholder="User Name" />
        </div>




        <button type="submit" class="btn btn-success">Register</button>
        </form>
        </div>
        </div>
        </div>
);
}
}

export default CustomerOnlyRegistration;
